@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,700);
.movie-details {
  position: fixed;
  border-radius: 0;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  z-index: 10000;
  background: #fff;
  -webkit-animation: movie-details 0.75s ease-in-out;
          animation: movie-details 0.75s ease-in-out;
  background-position: center center;
  background-size: 100% 100%;
  overflow: scroll;
  display: flex;
  justify-content: center;
  align-items: center;
}

.movie-details svg {
  position: absolute;
  top: 1.15rem;
  right: 2rem;
  fill: transparent;
  stroke: #fff;
  stroke-width: 0.15rem;
  width: 4rem;
  height: 4rem;
  cursor: pointer;
}

.movie-details--hide {
  position: absolute;
  top: 2rem;
  left: 2rem;
  background: none;
  border: none;
  font-size: 2rem;
  color: #fff;
  cursor: pointer;
}

.details {
  position: absolute;
  top: 40%;
  color: #fff;
  padding: 1rem;
}

.movie-details::-webkit-scrollbar {
  display: none;
}

.details p {
  font-size: 1.45rem;
  margin: 1rem 0;
}

.details p span:nth-child(2) {
  margin: 0 1rem;
}

.details p span:nth-child(2)::before {
  content: '|';
  margin-right: 1rem;
}

.details h4 {
  font-size: 2.25rem;
  font-weight: 700;
  letter-spacing: 1.25px;
}

p.movie-details--overview {
  line-height: 1.45;
  font-size: 1.25rem;
}

.cast {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.cast h4 {
  width: 100%;
  margin-bottom: 1rem;
}

.cast span {
  font-size: 1.25rem;
  margin: 1rem 0;
  -webkit-animation: fade-cast 1.5s ease-in-out;
          animation: fade-cast 1.5s ease-in-out;
}

.cast span::after {
  content: '|';
  margin: 0 1rem;
}

.see-more-cast {
  background: none;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 2rem;
  padding: 1rem 2rem;
  cursor: pointer;
  outline: none;
}

.play-trailer {
  background: none;
  border: none;
  margin: 0 auto;
  opacity: 0.75;
  cursor: pointer;
  -webkit-transition: opacity 0.5s ease-in;
  transition: opacity 0.5s ease-in;
  outline: none;
  text-align: center;
  margin-top: -70%;
}

.play-trailer:hover {
  opacity: 1;
}

.play-trailer img {
  width: 5rem;
  height: 5rem;
}

.cast-list {
  overflow-y: hidden;
  width: 100%;
  list-style: none;
  height: 20rem;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  background: transparent;
}

.movie-details ul::-webkit-scrollbar {
  display: none;
  width: 0;
  background: transparent;
}

.cast-list li {
  position: absolute;
  width: 10rem;
  box-shadow: 0.5rem 0.25rem 1rem rgba(0, 0, 0, 0.25);
  border-radius: 1.5rem;
  -webkit-transition: all 0.35s ease-in;
  transition: all 0.35s ease-in;
  cursor: pointer;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.cast-list li p {
  position: absolute;
  bottom: -1rem;
  background: rgba(20, 20, 20, 0.745);
  padding: 0.5rem 0;
  border-radius: 0 0 1.5rem 1.5rem;
  width: 10rem;
  text-align: center;
}

.cast-list li img {
  width: 10rem;
  border-radius: 1.5rem;
}

@-webkit-keyframes movie-details {
  0% {
    -webkit-transform: translate3d(-100%, -100%, 0);
            transform: translate3d(-100%, -100%, 0);
    opacity: 0;
  }

  75% {
    opacity: 0.5;
  }

  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes movie-details {
  0% {
    -webkit-transform: translate3d(-100%, -100%, 0);
            transform: translate3d(-100%, -100%, 0);
    opacity: 0;
  }

  75% {
    opacity: 0.5;
  }

  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes fade-cast {
  0% {
    -webkit-transform: translate3d(-20px, 0, 0);
            transform: translate3d(-20px, 0, 0);
  }

  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes fade-cast {
  0% {
    -webkit-transform: translate3d(-20px, 0, 0);
            transform: translate3d(-20px, 0, 0);
  }

  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}
.filter-menu {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  height: 10rem;
  padding: 0;
}

.filter-menu ul {
  overflow-x: scroll;
  overflow-y: hidden;
  width: 100%;
  list-style: none;
  height: 10rem;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  background: transparent;
}

.filter-menu ul li {
  background: -webkit-linear-gradient(315deg, rgb(184, 27, 35), rgba(211, 31, 31, 0.815));
  background: linear-gradient(135deg, rgb(184, 27, 35), rgba(211, 31, 31, 0.815));
  color: #fff;
  position: absolute;
  width: 10rem;
  box-shadow: 0.15rem 1.15rem 2.5rem -0.9rem rgba(233, 29, 29, 0.818);
  border-radius: 0.5rem;
  -webkit-transition: all 0.35s ease-in;
  transition: all 0.35s ease-in;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
  margin: 0 1rem;
  font-size: 1.35rem;
  font-weight: 700;
}

.filter-menu ul::-webkit-scrollbar {
  display: none;
  width: 0;
  background: transparent;
}
.main-header {
  width: 100%;
  position: relative;
  background: rgba(7, 7, 7, 0.89);
  padding: 1rem;
  box-shadow: 0 0 20px rgba(33, 30, 58, 0.35);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.main-header img {
  width: 5rem;
  height: 5rem;
}

#movie-search {
  width: 80%;
  padding: 1rem;
  height: 6rem;
  position: relative;
  margin: 0;
  margin-left: auto;
}

#movie-search > input {
  display: block;
  width: 100%;
  margin: 0;
  margin-left: auto;
  padding: 1rem 2.5rem;
  height: 4rem;
  border-radius: 2rem;
  border: none;
  border: 1px solid rgba(255, 255, 255, 0.274);
  outline: none;
  -webkit-transition: box-shadow 0.55s ease-in;
  transition: box-shadow 0.55s ease-in;
  background: transparent;
  color: #fff;
  font-size: 1.55rem;
  letter-spacing: 1.2px;
  -webkit-animation: input-query 0.65s ease-out;
          animation: input-query 0.65s ease-out;
}

#movie-search > button {
  position: absolute;
  top: 1.3rem;
  right: 1.5rem;
  border: none;
  font-size: 2rem;
  cursor: pointer;
  padding: 0.7rem 1.5rem;
  background: none;
  outline: none;
}

#movie-search > button > svg{
  fill: rgb(255, 255, 255);
}

.movie-list,
.favorites-list {
  list-style-type: none;
  padding: 2rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.movie {
  position: relative;
  width : 100%;
  margin: 1.5rem auto;
  display: flex;
  justify-content: flex-start;
  -webkit-animation-name: fade-in;
          animation-name: fade-in;
  -webkit-animation-duration: 0.55s;
          animation-duration: 0.55s;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  box-shadow: 0 0 10px rgba(0,0,0,0.25);
  border-radius: 0.5rem;
}

.movie .img {
  width: 40%;
  height: 25rem;
  background-position: center center;
  background-size: cover;
  border-radius: 0.5rem 0 0 0.5rem;
  -webkit-animation-name: fade-in;
          animation-name: fade-in;
  -webkit-animation-duration: 0.55s;
          animation-duration: 0.55s;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
}

.infos {
  width: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding-left: 2rem;
}

.main-infos {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.rating {
  background: rgb(22, 19, 31);
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  text-align: center;
  position: relative;
}

.score {
  text-align: center;
  color: #fff;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto auto;
  border: 3px solid rgb(30, 243, 30);
  border-radius: 50%;
  width: 3.4rem;
  height: 3.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.1rem;
  font-weight: 700;
}

.title {
  width: 60%;
  margin-left: 1rem;
  font-size: 1.5rem;
  cursor: pointer;
}

.release {
  width: 100%;
  font-size: 1.4rem;
  color: rgba(0,0,0,0.75);
  margin-top: 1rem;
  text-align: center;
}

.overview {
  width: 100%;
  padding: 0.5rem;
  line-height: 1.25;
  font-size: 1.3rem;
  margin-top: 0;
}

.movie-detail,
.movie-detail-on {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  margin: 0 auto;
  height: 100%;
  background: rgb(17, 15, 15);
  border-radius: 5px;
  display: none;
}

.movie-detail-on {
  display: block;
}

.video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 35px;
  height: 0;
  overflow: hidden;
  margin-top: -13%;
}

.movie-detail-on iframe {
  position: absolute;
        top:22px;
        left: 0;
        width: 100%;
        height: 100%;
}

.close-modal {
  position: fixed;
  top: 10px;
  left: 10px;
  z-index: 10000;
  cursor: pointer;
  border: none;
  background: none;
  font-size: 29px;
  color: #fff;
}

.favorite {
  position: absolute;
  left: 1rem;
  top: 1rem;
  font-size: 2rem;
  color: #fff;
  cursor: pointer;
  -webkit-transition: color 0.45s ease-in;
  transition: color 0.45s ease-in;
}

.favorite-true {
  color: rgb(255, 191, 15);
}

/* Media querys */

@media (min-width: 760px) {
  #movie-search {
    width: 66%;
  }

  .movie {
    width: 45%;
  }
}

@media (min-width: 1100px) {
  #movie-search {
    width: 45%;
  }

  .movie {
    width: 30%;
  }
}

/* ANIMAIONS*/

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(-50px, 0, 0);
            transform: translate3d(-50px, 0, 0);
  }
  100%{
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(-50px, 0, 0);
            transform: translate3d(-50px, 0, 0);
  }
  100%{
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes input-query {
  0% {
    width: 0;
  }
  100%{
    width: 100%;
  }
}

@keyframes input-query {
  0% {
    width: 0;
  }
  100%{
    width: 100%;
  }
}
.auto-completion {
  position: absolute;
  top: 8rem;
  width: 60%;
  list-style: none;
  left: 0;
  right: 0;
  margin: 0 auto;
  z-index: 100000;
  background: rgba(7, 7, 7, 0.89);
  border-radius: 0 0 2rem 2rem;
  color: #fff;
}

.auto-completion li {
  font-size: 1.5rem;
  margin: 0.5rem 0;
  cursor: pointer;
  border-bottom: 1px solid rgba(255, 255, 255, 0.274);
  width: 100%;
  padding: 1rem;
}

.auto-completion li:last-of-type {
  border: none;
}
html {
  box-sizing: border-box;
  font-size: 10px;
  min-height: 100%;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Raleway', sans-serif;
}
